.cartCount{
    /* background: ${props => props.color}; */
    background-color: orange;
    border-radius: 50%;
    padding: 4px 8px;
    position: absolute;
    right: 10px;
    top: -5px;
    font-size: 12px;
    /* visibility: ${props => (props.show? "visible": "hidden")}; */

}
.cartIconContainer{
    position: relative;
    cursor: pointer;

}
.title{
    font-size: 30px;
    color: #fff;
    font-weight: 600;
    font-family: 'Times New Roman', Times, serif;
    text-transform: uppercase;
    margin-left: 20px ;
    /* &:hover{color:#f00; font-size: 35px}; */

}
.nav{
    height: 70px;
	background-color: #4267b2;
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: relative;

}
.cartIcon{
    height: 48px;
    margin-right: 20px;

}