body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.main{
  width:70%;
  margin: 0 auto;
  margin-top: 30px;
  padding: 0 20px;
  box-sizing:border-box;
}
.movie-card{
  background: #fff;
  padding: 0;
  border: none;
  border-radius: 10px;
  box-shadow: 0 1px 3px 0 #d4d4d5, 0 0 0 1px #f1f1f1;
  display: flex;
}
.movie-card .left{
  flex: 1;
  padding: 5px;
}
.movie-card .left img{
  height: 250px;
}
.movie-card .right{
  position: relative;
  flex: 10;
}
.movie-card .title{
  padding: 10px 20px;
  font-size: 25px;
  font-weight: 600;
}
.movie-card .price{
  margin-top: 20px;
  padding: 0 20px;
  font-weight: 500;
  color: rgb(0, 75, 0);
}
.footer{
  position: absolute;
  bottom: 0;
  display: flex;

  width: 100%;
  padding: 10px 20px;
  box-sizing: border-box;
}
.footer .rating{
  font-size: 20px;
  font-weight: 600;
  flex: 1;
}
.footer .stars{
  width: 20px;
  height: 20px;
}
.footer button{
  font-size: 15px;
  padding: 7px 10px;
  text-shadow: none;
  border-radius: 3px;
  border: none;
  font-weight: 600;
}
.favourite-btn{
  background-color: #bc16dd;
  color: #fff;
  margin: 5px;
}
.cart-btn{
  background-color: #251bb1;
  color: #fff;
  margin: 5px;
}
.footer .star-dis {
  font-size: 20px;
  font-weight: 600;
  flex:1;
  position: relative;
}
.footer .str-btn {
  width: 7px;
  height: 7px
}
.unfav-btn{
  background-color: red;
  color: #f1f1f1;
  text-shadow: none;
  margin: 5px;
}
.re-cart-btn{
  background-color: rgb(27, 152, 201);
  color: white;
  margin: 5px;
}
